.neo-markdown-component {
    background-color: white;
    border-radius   : 8px;
    letter-spacing  : 1px;
    margin          : 0 auto;
    max-width       : 1200px;
    overflow        : unset;
    padding         : 0 3rem;
    width           : -webkit-fill-available;

    @media (max-width: 600px) {
        overflow-x: hidden;
        padding   : 1rem 1rem 0;
    }

    a {
        color: #3E63DD;;
    }

    blockquote {
        background-color: var(--gray-100);
        border-left     : 4px solid var(--sem-color-surface-primary-background);
        font-style      : italic;
        margin-left     : 0;
        padding         : 5px 5px 5px 15px;
    }

    details {
        margin: 1em 0 1em 0;
    }

    details[open] {
        margin-bottom: 2em;
    }

    details summary {
        align-items: center;
        color      : #555;
        cursor     : pointer;
        display    : flex;
        font-weight: bold;
        list-style : none;
        transition : margin 300ms ease-out;
    }

    details summary::before {
        content: "\f055";
    }

    details[open] summary::before {
        content: "\f056";
    }

    details summary::before {
        color      : #c4c4c4;
        font-family: var(--fa-style-family, "Font Awesome 7 Free");
        font-size  : 1em;
        font-weight: var(--fa-style, 900);
        margin     : 0.4em 0.4em 0.4em 0;
    }

    h1.neo-h1 {
        color: #3E63DD;
    }

    h2.neo-h2,
    h3.neo-h3 {
        code {
            font-family: inherit !important;
            font-size  : 1em !important;
            font-weight: inherit !important;
        }
    }

    .lab {
        box-shadow   : 0 4px 8px 0 rgba(0, 0, 0, 0.2);
        font-size    : 1em;
        margin-bottom: 1em;
        padding      : 2px 16px;
        transition   : 0.3s;

        &:hover {
            /* On mouse-over, add a deeper shadow */
            box-shadow: 0 8px 16px 0 rgba(0, 0, 0, 0.2);
        }
    }

    li::marker {
        color: #3E63DD;
    }

    p {
        margin: 0.5em 0 0.7em 0;
    }

    pre code.language-text {
        &:not(.hljs) {
            background-color: #F8F8F8;
            border          : thin solid lightgray;
            display         : block;
            font-size       : 13px;
            overflow-x      : auto;
            padding         : 12px;
            white-space     : pre;
        }
    }

    pre[data-javascript] {
        border        : thin solid lightgray;
        border-radius : 4px;
        letter-spacing: 0;
        overflow-x    : scroll;
        padding       : 12px;
    }

    summary::-webkit-details-marker {
        display: none;
    }
}
